<template>
	<div class="panel">
		<h2>热销商品Top10</h2>
		<div ref="barChart" class="chart">图表</div>
		<div class="panel-footer"></div>
	</div>
</template>

<script>
	export default {
		name: 'BarPanel',
		data() {
			return {
				listX: [],
				listY: []
			}
		},
		methods: {
			drawChart() {
				let chart = this.$echarts.getInstanceByDom(this.$refs.barChart);
				if (!chart) {
					chart = this.$echarts.init(this.$refs.barChart);
					this.$once('hook:beforeDestroy', () => {
						this.$echarts.dispose(chart)
					})
				}
				let option = {
					color: ["#2f89cf"],
					tooltip: {
						trigger: 'axis',
						axisPointer: {
							type: 'shadow'
						},
						backgroundColor: "rgba(50, 50, 50, 0.7)",
						borderWidth: 0,
						textStyle: {
							color: "#ffffff"
						},
						hideDelay: 0
					},
					grid: {
						top: "10px",
						left: "10px",
						right: "5px",
						bottom: "2%",
						containLabel: true
					},
					xAxis: [{
						type: 'category',
						data: this.listX,
						axisTick: {
							alignWithLabel: true
						},
						axisLabel: {
							color: "#fff"
						},
						axisLine: {
							show: false
						}
					}],
					yAxis: [{
						type: 'value',
						axisLabel: {
							color: "#fff"
						},
						axisLine: {
							lineStyle: {
								color: "rgba(255,255,255,0.6)",
								width: "1",
								type: "solid"
							}
						},
						splitLine: {
							lineStyle: {
								color: "rgba(255,255,255,.1)"
							}
						}
					}],
					series: [{
						name: '销量',
						type: 'bar',
						barWidth: '40%',
						data: this.listY,
						itemStyle: {
							borderRadius: 4
						}
					}]
				};
				chart.setOption(option);
				window.addEventListener("resize", chart.resize);
			}
		},
		mounted() {
			this.$axios.get(process.env.BASE_URL + 'expend/sale.dat').then(resp => {
				for (let i = 0; i < resp.data.length; i++) {
					this.listX.push(resp.data[i][0]);
					this.listY.push(resp.data[i][1]);
				}
				this.drawChart();
			})
		}
	}
</script>

